	module.exports = {"data":{"markdownRemark":{"html":"<p>当時製作中だった漫画(のちに RPG 化を企んで挫折) “CATS STORY” のイントロにおけるワンシーンを 3DCG で再現。 モデリングツールは DoGA-L2 を使用。当時は 3D オーサリングツールといえば、高価格な Lightwave3D と低価格な DoGA-L シリーズしか知らなかった。  このシーンは研究所幹部と所員が人工衛星 “方舟” を視察しているシーン。この人工衛星に選ばれた生き物のみを積載し宇宙へ飛ばし、宇宙から星のエネルギーを凝縮したようなやつを地球に打ち込んで地上を殲滅する、という任務を請け負う。別の漫画になるけどバーンパレスに性格上近いかも。 当初ネコ人間カルテットがドタバタ展開を起こすギャグ漫画だったのに、いつの間にか人間とネコ種族との戦争を描いたシリアス &#x26; ガチ鬱ストーリーに変貌していた。</p>","frontmatter":{"cover":null,"date":"1999-08-13T22:00:10+09:00","strDate":"1999/8/13","title":"CATS STORY イメージCG"}}},"pathContext":{"path":"/ja/blog/1999/08/13-cats-story/","langKey":"ja"}}

